@mixin visually-hidden {
  @include position-absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@mixin list-reset {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

@mixin segoe-ui {
  font-family: "Segoe UI", "Tahoma", sans-serif;
}

@mixin position-absolute {
  position: absolute;
  content: "";
}

@mixin wrapper-margin {
  margin-right: 50px;
  margin-left: 50px;

  @media (min-width: $desktop-width) {
    margin-right: 190px;
    margin-left: 190px;
  }
}